.hero.container {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(83deg, #111 36.85%, rgba(17, 17, 17, 0) 60.05%), url("Hero_desktop.57ce28b5.png");
  background-position: center;
  background-repeat: repeat, no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  font-family: Roboto, sans-serif;
}

.intro {
  color: #fff;
  max-width: 232px;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 20px;
  padding-top: 40px;
  padding-left: 20px;
  display: flex;
}

.title {
  font-size: 32px;
  font-weight: 700;
}

.text {
  max-width: 177px;
  max-height: 66px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  overflow: hidden;
}

.buttons {
  gap: 8px;
  padding-bottom: 54px;
  font-family: inherit;
  display: flex;
}

.hero-btn {
  text-align: center;
  border-radius: 74px;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  display: inline-flex;
}

.hero-btn.cta {
  color: var(--black-color, #111);
  background: linear-gradient(141deg, #ffc226 9.4%, #f84119 91.91%);
  border: none;
  margin-left: 20px;
}

.hero-btn.more-details {
  color: #fff;
  background: none;
  border: 1px solid #fff;
  margin-left: 8px;
}

@media screen and (min-width: 768px) {
  .hero.container {
    height: 372px;
  }

  .intro {
    max-width: 445px;
    margin-bottom: 32px;
    padding-top: 62px;
    padding-left: 32px;
  }

  .title {
    font-size: 44px;
  }

  .text {
    max-width: 254px;
    max-height: 49px;
  }

  .buttons {
    padding-bottom: 63px;
  }

  .hero-btn {
    height: 40px;
    font-size: 14px;
  }

  .hero-btn.cta {
    margin-left: 32px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .hero-btn {
    padding: 10px 24px;
  }
}

@media screen and (min-width: 1280px) {
  .hero.container {
    height: 660px;
  }

  .intro {
    max-width: 670px;
    gap: 16px;
    margin-left: 135px;
    padding-top: 118px;
  }

  .title {
    font-size: 64px;
  }

  .text {
    max-width: 367px;
    max-height: 59px;
    font-size: 16px;
  }

  .buttons {
    margin-left: 135px;
  }

  .hero-btn {
    height: 48px;
    cursor: pointer;
    font-size: 16px;
  }
}

